		"image": "economie-finance",
		"pos": 3,
		"intro": "<a href=\"http://www.europe1.fr/politique/sur-tf1-le-pen-en-candidate-du-peuple-contre-le-candidat-de-loligarchie-3311654\">Face à Emmanuel Macron, Marine Le Pen aimait se présenter</a> comme la « candidate du peuple » luttant contre « le règne de l&#39;argent, l&#39;idée que le marché est le patron », contre « l&#39;oligarchie », « la finance » ou la « mondialisation sauvage ».<br>Depuis la crise de 2008, le Front National a fait mine de changer, prônant un état fort face à la mondialisation et la dérégulation. Pourtant, dans les faits, alors que Jean-Marie Le Pen se présentait comme « le Reagan Français », <a href=\"http://bfmbusiness.bfmtv.com/france/comment-le-fn-est-passe-de-l-ultra-liberalisme-a-l-etat-protecteur-933363.html\">le FN a juste mis un peu d&#39;eau dans son vin</a>. Les termes &quot;actionnaire&quot;, &quot;dividende&quot; &quot;cac40&quot;, &quot;oligarchie&quot; ou &quot;ouvrier&quot; ne figurent pas du tout dans son programme, &quot;finance&quot; et &quot;marché&quot; quasiment pas, mais on y trouve beaucoup de recettes économiques finalement très libérales, comme la <a href=\"https://www.marine2017.fr/wp-content/uploads/2017/02/texte-chiffrage-version-finale-.pdf\">baisse des recettes de l&#39;État (impôts et taxes) et des dépenses publiques</a>. Accompagnées d&#39;une forte dose de « patriotisme économique » et de protectionnisme pour défendre le capital français, mais pas vraiment les travailleurs, ni l’écologie.",
